.venue {
	 h3 {
	 	font-size: 22px;
	 	font-weight: 400;
	 	margin-bottom: 8px;
	 }

	 .venue-phone {
	 	display: flex;
	 	align-items: center;
	 	color: #666666;
	 	margin-top: 0;

	 	img {width: 20px;padding-right: 4px;}

	 }
	 .venue-map {
	 	margin-top: 20px;
	 	height: 200px;

	 	@media(min-width: 990px){
	 		height: 450px;
	 	}
	 }
	 	p.offline-warning {
	 		font-style: italic;
	 		font-size: 14px;
	 	}

	 .venue-address {color: #666;margin-top: 8px;}
}
