header.header {
  background-color: #000;
  padding: 15px 0;
  background-size: cover;
  height:52px;
  box-sizing: border-box;
  transition: height ease-in-out 400ms;

  &.minHeader{
    @media(min-width: 990px){
      background-image:none !important;
      height: auto;
    }
  }

  h1 {
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    font-size: 18px;
    margin:0;
    color: #fff;
    transition: all ease-in-out 400ms;
    @media(min-width: 990px){
      font-size: 24px;
    }
  }

  p.date {
    color:#fff;
    font-weight: 600;
    position:relative;
    padding-left: 28px;

    &:before {
      content: '\e878';
      font-family: 'Material Icons';
      font-size: 22px;
      color: #fff;
      position:absolute;
      left:0;
      top: -1px;
      font-weight: 400;
    }
  }

  &.fullHeader {
    height:190px;

    h1 {
      font-size: 30px;
      font-weight: 800;
      margin-top: 20px;
    }
  }
}

nav.navbar {
  padding:8px 0;
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #000;
  z-index:9999;

  @media(min-width: 990px) {
    position:relative;
  }


  ul {
    list-style: none;
    padding:0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    li {
      color: #ccc;
      font-size: 11px;
      cursor: pointer;

      @media(min-width: 990px) {
        display: flex;
        align-items: center; 
        font-size: 16px;
        padding-bottom:10px;
      }

      &.active {
        color: #BA00FF;
      }

      &:hover {
        color: #BA00FF;
      }

      & > *  {
        display: block;
        text-align:center;
        @media(min-width: 990px) {
          display: inline-block;
        }
      }

      span {
        @media(min-width: 990px) {
          padding-left: 8px;
        }
      }


    }
  }
}

.appContent {
  padding-bottom: 60px;
  h2 {font-size: 30px;margin: 20px 0 3px;}
  .home p {
    line-height: 1.5;
    @media(min-width: 990px){
      font-size: 18px;
    }
  }
}



