.schedule {

	.sched-date {margin: 0; color: #666666}

	.sched-list {

		.sched-block {

			h3 {
				font-size: 15px;
				color:#666666;
				font-weight: 400;
				margin: 20px 0 5px;
			}

			.sched-container {
				padding-top: 5px;

				@media(min-width: 990px){
					display: flex;
					margin-left: -10px;
					margin-right: -10px;
				}

				.event {
					display: block;
					width: 100%;
					border-radius: 4px;
					box-shadow: 0 1px 2px rgba(0,0,0,0.25);
					box-sizing:border-box;
					padding: 12px;
					background-color: #FFFFFF;
					min-height: 54px;
					margin-bottom: 15px;

					@media(min-width: 990px){
						max-width: 31%;
						margin-left:10px;
						margin-right: 10px;
					}

					//&:nth-child(even) {background-color: rgba(25,241,149,.32);}
					&.color_01 {background-color: rgba(255,241,162,.6);}
					&.color_02 {background-color: rgba(0,31,202,.28);}
					&.color_03 {background-color: rgba(237,0,255,.2);}
					// &.color_02 {background-color: rgba(25,72,241,.22);}
					// &.color_03 {background-color: rgba(255,117,113,.6);}
					&.color_04 {background-color: rgba(74,183,38,.28);}

					span {
						display: block;

						&.ev-title {
							font-size: 17px;
							font-weight: 600;
							margin-bottom: 4px;
						}
						&.ev-speaker {
							margin-bottom: 4px;
							font-style: italic;
						}
						&.ev-place {font-size: 14px;}
					}
				}
			}
		}
	}
}