.speakers {
	.speakers-container {
		padding: 0 30px;

		@media(min-width: 768px){
			display: flex;
			flex-wrap: wrap;
		}

		@media(min-width: 990px){
			padding:0;
			margin-left: -20px;
			margin-right: -20px;
			justify-content: space-between;
		}
		.speaker {
			padding:10px;
			margin: 20px 0;
			position:relative;
			box-sizing: border-box;

			@media(min-width: 768px){
				max-width: 300px;
				padding:20px;
			}
			.speaker-img {
				overflow: hidden;
				z-index:1;
				display: block;
				transition: all ease-in-out 200ms;

					img {
						width: 100%;
						height: auto;
						border-radius:50%;
					}
			}

			.speaker-name {

				h3 {
					text-align:center;
					position: relative;
					padding:8px;
					background-color: #fff;
					margin-top: -40px;
					margin-bottom: 0;
					z-index:50;
					font-size: 24px;
				}
			}

			ul.speaker-social {
				list-style: none;
				padding: 0;
				margin: 0 36px;
				display: flex;

				li {
					width: 50%;
					text-align: center;

					&:nth-child(2) {padding-left:16px;}

					a {
						display: flex;
						align-items:center;
						text-decoration: none;
						color: #38209B;
						font-weight: 600;

						img {width: 24px;height: 24px;padding-right: 6px;}
					}
				}
			}
		}
	}
}